:root {
    color-scheme: light dark;
}
body {
    background-color: rgb(18, 17, 17);
    color: white;
    height: 100%;
}
a:link, a:hover, a:active, a:visited {
    text-decoration: none;
}



/*** CALLOUT ****/
a.store-badge {
    display: inline-block;
    height: 0;
    overflow: hidden;
    background-repeat: no-repeat;
    margin-right: 0.5em;
    width: 122px;
    padding-top: 36px;
    background-size: 122px 36px;
}
.badge-app-store {
    background-image: url('../images/app-store-badge-o.svg');
    background-image: url('../images/app-store-badge.svg');
}
.badge-app-store:hover {
    background-image: url('../images/app-store-badge-o.svg');
}
.badge-google-play {
    background-image: url('../images/google-play-badge-o.svg');
    background-image: url('../images/google-play-badge.svg');
}
.badge-google-play:hover {
    background-image: url('../images/google-play-badge-o.svg');
}